#consoleframe{
    width: 500px;
    height: 700px;
    
    z-index: 0;
    margin: auto;
    margin-top: -700px;
    background-color: rgba(27, 27, 27, 0.271);
  
    border-radius: 10px 10px;
    box-shadow: -10px 10px 4px -10px rgba(178, 178, 178, 0.434) inset;
    overflow:hidden;
    transition: 1.5s;
    position:relative;
    
    opacity: 0;
    
}

#console{
    width: 90%;
    height: 75%;
    
    background-color: rgba(27, 27, 27, 0.271);
    margin: auto;
    margin-top: 5%;
    border-radius: 10px 10px;
    box-shadow: -10px 10px 5px -10px rgba(178, 178, 178, 0.13) inset;
    transform: rotateY(180deg);
}
#console_text{
    width: 90%;
    height: 100%;
    position: relative;
    margin: auto;
    margin-top: 3%;
    font-size: 13px;
    overflow-y: auto;

    color: rgb(129, 211, 57);
}


#stop_button{
    width:90%;
    height: 11%;
    margin: auto;
    margin-top:10%;
  
    background-color: rgba(27, 27, 27, 0.271);
    border-radius: 10px 10px;
    position: relative;
    display: flex;
    align-items: center;
    
    cursor: pointer;
    border-radius: 10px 10px;
    box-shadow: -10px 10px 4px -10px rgb(163, 30, 30) inset;
    transform: rotateY(180deg);
    overflow:hidden;
  }
  
  #stop_button:hover{
    background-color: rgba(45, 45, 45, 0.271);
  }
  
  #stop_text{
    width: auto;
    height: auto;
    margin: auto;
    text-align: center;
    color: red;
    font-size: 40px;
    user-select: none;
    /* letter-spacing : 2px;             
      text-shadow    : 
          2px  2px 1px rgba(30, 163, 39, 0),
          -2px  2px 1px rgba(30, 163, 39, 0),
          2px -2px 1px rgba(30, 163, 39, 0),
          -2px -2px 1px rgb(30, 163, 38),
          2px  8px 1px rgba(45, 126, 50, 0),
          0px  2px 1px #000000,
          -2px  0px 1px #000000,
          0px -2px 1px #000000;  */
    
    position: relative;
  }