::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: rgb(28, 28, 28);
}

::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 1px rgb(129, 129, 129);
}

.usermessage{
  width: 500px;
  height: 150px;

  margin: auto;
  margin-top: 5%;
  background-color: rgba(243, 30, 30, 0.37);
  border-radius: 10px 10px;
  box-shadow: 10px 10px 4px -10px rgba(178, 178, 178, 0.434) inset;
  color: rgb(196, 196, 196);
}
.text_center{
  text-align: center;
}

body{
  background-color: black;
}

header{
  width: 500px;
  height: auto;
  color: rgb(21, 61, 243);
  margin: auto;

  text-align: center;
  border-bottom:solid;
  border-color:rgb(255, 255, 9);
  border-width:4px;
  user-select: none;
}

.bosframe{
  width: 500px;
  height: 700px;
  margin: auto;
  margin-top: 5%;
  
  position: relative;
  z-index: 3;
}

#mainframe{
  width: 500px;
  height: 700px;
  margin: auto;
  margin-top: 5%;

  background-color: rgba(27, 27, 27, 0.271);
  border-radius: 10px 10px;
  position: relative;
  
  border-radius: 10px 10px;
  box-shadow: 10px 10px 4px -10px rgba(178, 178, 178, 0.434) inset;
  overflow:hidden;
  
  z-index: 2;
  transform: rotateY(0deg);
  transition: 1.5s;
  color: rgb(129, 211, 57);
  
  opacity: 1;
  /* display: none; */
  
}

#target_select_list{
  width:90%;
  height: 50%;
    
  text-align: center;
  margin: 20px auto;
  background-color: rgba(125, 125, 125, 0.052);

  border-radius: 10px 10px;
  box-shadow: 10px 10px 5px -10px rgba(178, 178, 178, 0.13) inset;
  overflow:hidden;
  
  overflow-y: auto;
}

.select_list_text{
  cursor: pointer;
}
.select_list_text:hover{
  color: red;
}

.input_target, .input_ms, .input_count {
  width: 90%;
  height: 5%;
  /* background-color: white; */
  margin: auto;
  margin-top: 5%;
}


#select_target, #select_ms, #select_count{
  width: 70%;
  height: 90%;

  border-bottom:solid;
  border-color:rgb(30, 163, 38);
  border-width:2px;
  overflow-y: hidden;
  float: left;
  position: relative;
  /* background-color: rgb(251, 251, 251); */
}


.text_target,.text_ms,.text_count{
  width: 60%;
  z-index: 0;
  text-align: center;
  background-color: rgba(183, 183, 183, 0);
  color: rgb(9, 43, 12);
  user-select: none;
  position: absolute;
}

#select_target_button,#select_ms_button,#select_count_button{
  width: 30%;
  height: 90%;
  float: left;
  text-align: center;
  
  border-bottom:solid;
  user-select: none;
  
  color: rgb(9, 43, 12);
  border-color:rgb(9, 43, 12);
  border-width:2px;
} 

.select_option{
  width: 90%;
  height: 5%;
  margin: auto;
  margin-top: 1%;
  position: relative;
}
#select_option_low, #select_option_middle, #select_option_High, #select_option_kill,#select_url_lang{
  width: 17.5%;
  height: 100%;
  border-bottom:solid;
  border-color:rgb(9, 43, 12);
  border-width:2px;
  float: left;
  position: relative;
  cursor: pointer;
  color: rgb(9, 43, 12);
  text-align: center;
  font-size: 90%;

}

