  border-color:rgb(255, 255, 9);
  color: rgb(21, 61, 243);
} */
#select_url_lang{
  width: 30%;
  overflow-y:hidden;
}
#select_url_lang_text{
  width: 100%;
  height: 100%;
  z-index: 0;
  text-align: center;
  position: absolute;
  
}
#select_url_lang_input{
  width: 100%;
  height: 50%;
  position: relative;
  font-size: 100%;
  margin-top: 10%;
  color: red;
}


#run_button{
  width:90%;
  height: 11%;
  margin: auto;
  margin-top:3%;

  background-color: rgba(27, 27, 27, 0.271);
  border-radius: 10px 10px;
  position: relative;
  display: flex;
  align-items: center;
  
  cursor: pointer;
  border-radius: 10px 10px;
  box-shadow: 10px 10px 4px -10px rgb(30, 163, 38) inset;
  overflow:hidden;
}

#run_button:hover{
  background-color: rgba(45, 45, 45, 0.271);
}

#run_text{
  width: auto;
  height: auto;
  margin: auto;
  text-align: center;
  color: rgb(9, 43, 12);
  font-size: 40px;
  user-select: none;
  position: relative;
}